:root {
  --section-overlay: rgba(0, 0, 0, 0.7);
  --section-overlay-text:rgba(255, 255, 255 );
  --background-overlay: rgba(0, 0, 0, 0.2);
  --button-color:rgba(255, 255, 255);
  --button_hover:rgba(255, 255, 255, 0.723);
}

[dark-theme="dark"]{
  --section-overlay: rgb(255, 255, 255, 0.7);
  --section-overlay-text:rgba(0, 0, 0 );
  --button-color:rgb(7, 20, 197);
  --button_hover:rgba(32, 44, 210, 0.888);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  transition: background-color 0.5s ease;
}

.overlay {
  width: 100%;
  height: 100vh;
  background-color: var(--background-overlay);
}

@media screen and (max-width:768px) and (min-width:501px){
  .overlay{
    height: 125vh;
  }
}


@media screen and (max-width:500px) and (min-width:361px){
  .overlay{
    height: 140vh;
  }
}

@media screen and (max-width:360px){
  .overlay{
    height: 150vh;
  }
}

.container {
  max-width: 900px;
  margin: auto;
  max-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}


/* top */

.section {
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
  color: var(--section-overlay-text);
}

.section__inputs,
.section__temperature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--section-overlay);
}

.section__inputs > input {
  border: 0.8px solid white;
  border-radius: 0.4rem;
  background-color: transparent;
  color: white;
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 200;
}

.section__inputs > input:focus {
  outline: none;
}

.section__inputs > button {
  padding: 10px 50px;
  border: none;
  border-radius: 0.4rem;
  font-size: 20px;
  font-weight: 500;
  background-color: var(--button-color);
  color: var(--section-overlay);
}

@media screen and (max-width: 540px) {
  .section__inputs > button {
    padding: 10px 5vw;
    margin: 10px;
  }
  .section__inputs > input {
    font-size: 15px;
    padding: 0.4rem;
  }
}

.section__inputs > button:hover {
  cursor: pointer;
  background-color: var(--button_hover);
}

.icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon > h3 {
  font-size: 15px;
  font-weight: 200;
  text-transform: capitalize;
}

.temperature > h1 {
  font-size: 40px;
}

.message {
  justify-content: center;
  text-align: center;
  background-color: var(--section-overlay);
  color: white;
}

span {
  color: chartreuse;
}
